.dogEntry {
    padding: 5%;
    align-items: center;
    display: flex;
    flex-direction: column;
    transition: background-color 0.5s ease-in, box-shadow 0.3s ease-in;
}

.dogPortrait {
    object-fit: cover;
    border: 10px solid darkgray;
    border-radius: 50%;
    height: calc(250px + 2vmin);
    width: calc(250px + 2vmin);
    transition: box-shadow 0.3s ease-in, transform .3s ease-in;
}

.dogPortrait:hover {
    box-shadow: 0px 0px 10px 1px darkgray;
    transform: scale(1.1);
}

.dogPortraitPresent {
    border: 10px solid green;
}

.sectionText {
    font-size: calc(10px + 2vmin);
    text-align: center;
}

::-webkit-scrollbar {
    width: .4em;
    background-color: #282c34;
  }
::-webkit-scrollbar-thumb {
    background-color: white; 
  }